import React from 'react';
import './App.css';
import { Switch } from 'antd'

const logo = 'https://www.google.com/images/branding/googlelogo/2x/googlelogo_color_272x92dp.png'

function App() {
  const [animate, setAnimate] = React.useState(false)
  const toggleAnimate = () => {
    setAnimate(!animate)
  }

  const imageStyle = {
    maxHeight: 100
  }

  if (animate) imageStyle.animation = 'App-logo-spin infinite 10s linear'
  else imageStyle.animation = ''

  return (
    <div className={"App"}>
      <header className="App-header">
        <img src={logo} style={imageStyle} alt="logo"  />
        <p>
          Lakshya
        </p>
        <Switch onChange={toggleAnimate} />
        <a
          className="App-link"
          href="http://www.youtube.com"
          target="_blank"
          rel="noopener noreferrer"
        >
          Open Youtube
        </a>
      </header>
    </div>
  );
}

export default App;
